@mixin body {
  height: 100%;
  width: 100%;
  background-color: black;
  color: white;
  overscroll-behavior: none;
};
@mixin hiddenViewport{
  height: 0px;
}
@mixin sphere {
  box-shadow:
  inset 0 0 50px #fff,
  inset 20px 0 80px #f0f,
  inset -20px 0 80px #0ff,
  inset 20px 0 300px #f0f,
  inset -20px 0 300px #0ff,
  0 0 50px #fff,
  -10px 0 80px #f0f,
  10px 0 80px rgba(0, 255, 255, 0.438);
}


